/* кнопка Х */
.close-button {
    position: absolute;
    display: flex;
    top: 0;
    right: 0;
    width: 30px;
    height: 30px;
    margin: 8px;
    justify-content: center;
    align-items: center;
    background-color: transparent;
    border: none;

    cursor: pointer;

    &:hover,
    &:focus {
        fill: var(--accent-color);
    }
}

.close-button__icon {
    width: 30px;
    height: 30px;

    @media (prefers-color-scheme: dark) {
        fill: #c4c3c3;
    }

    &:hover {
        @media (prefers-color-scheme: dark) {
            fill: var(--accent-color);

            transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
        }
    }


}